"use client";
import { useState } from "react";
import axios from "axios";
import { Input, Button, Card } from "@hero-ui/react";

export default function Weather() {
  const [city, setCity] = useState("");
  const [weather, setWeather] = useState<any>(null);
  const [error, setError] = useState("");

  const fetchWeather = async () => {
    try {
      setError("");
      const apiKey = process.env.NEXT_PUBLIC_WEATHER_API_KEY;
      const response = await axios.get(
        `https://api.openweathermap.org/data/2.5/weather?q=${city}&units=metric&appid=${apiKey}`
      );
      setWeather(response.data);
    } catch (err) {
      setError("City not found. Please try again.");
      setWeather(null);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-900 text-white">
      <Card className="p-6 max-w-md w-full text-center bg-gray-800">
        <h1 className="text-2xl font-bold mb-4">Weather App</h1>
        <div className="flex gap-2">
          <Input
            placeholder="Enter city name"
            value={city}
            onChange={(e) => setCity(e.target.value)}
          />
          <Button onClick={fetchWeather} color="primary">
            Search
          </Button>
        </div>
        {error && <p className="text-red-400 mt-3">{error}</p>}
        {weather && (
          <div className="mt-4">
            <h2 className="text-xl font-semibold">{weather.name}, {weather.sys.country}</h2>
            <p className="text-lg">{weather.weather[0].description}</p>
            <p className="text-4xl font-bold">{weather.main.temp}Â°C</p>
            <p>Humidity: {weather.main.humidity}% | Wind: {weather.wind.speed} m/s</p>
          </div>
        )}
      </Card>
    </div>
  );
}
